<template>
  <el-table :data="bugList" border style="width: 100%" :height="tableHeight">
    <el-table-column type="index" width="45"></el-table-column>
    <el-table-column prop="id" align="center" width="70" label="编码"></el-table-column>
    <el-table-column prop="name" label="摘要">
      <template slot-scope="scope">
        <div>
          <i v-if="hasImage(scope.row.attachs)" class="fa fa-image margin-left-5"></i>
          <i v-if="hasVedio(scope.row.attachs)" class="fa fa-film"></i>
          <span class="bug-summary" @click='onBugEdit(scope.row)'>{{scope.row.name}}</span>
        </div>
      </template>
    </el-table-column>
    <el-table-column
      prop="level"
      width="80"
      :formatter="levelFormater"
      :filters="levelFilter"
      :filter-method="filterHandler"
      label="等级">
    </el-table-column>
    <el-table-column
      prop="state"
      width="80"
      :formatter="stateFormater"
      :filters="stateFilter"
      :filter-method="filterHandler"
      label="状态">
    </el-table-column>
    <el-table-column
      prop="commitUser"
      width="90"
      :formatter="userFormatter"
      :filters="userFilter"
      :filter-method="filterHandler"
      label="提交人">
    </el-table-column>
    <el-table-column prop="curUserId" width="110" label="当前处理人">
      <template slot-scope="scope">
        <bug-orbit :bug="scope.row"></bug-orbit>
      </template>
    </el-table-column>
    <el-table-column prop="createTime" width="100" :formatter="dateFormatter" label="创建时间"></el-table-column>
    <el-table-column label="操作" width="140">
      <template slot-scope="scope">
        <div class="quick-operation">
          <i class="fa fa-edit" @click="onBugEdit(scope.row)"></i>
          <i class="fa fa-copy" @click="onCopy(scope.row)"></i>
          <i class="fa fa-trash" @click="onDelete(scope.row)"></i>
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { mapMutations } from 'vuex'
import bugOrbit from './bug-orbit'
export default {
  data () {
    return {
      levels: [],
      levelFilter: [],
      states: [],
      stateFilter: [],
      userFilter: [],
      tableName: 'bugs'
    }
  },
  components: {
    bugOrbit
  },
  computed: {
    userList () {
      return this.$store.state.userList
    },
    bugList () {
      return this.$store.getters.filterBugList
    },
    tableHeight () {
      return this.$utils.getClientHeight() - 120
    }
  },
  watch: {
    bugList () {
      this.calcFilter()
    }
  },
  methods: {
    initData () {
      this.$enumService.getEnum('BUG_LEVEL').then((data) => {
        this.levels = data
        return this.$enumService.getEnum('BUG_STATE')
      }).then((data) => {
        this.states = data
        this.calcFilter()
      })
    },
    calcFilter () {
      this.levelFilter = []
      this.stateFilter = []
      this.userFilter = []

      this.levels.forEach(item => {
        this.levelFilter.push({
          text: item.name,
          value: item.id
        })
      })

      this.states.forEach(item => {
        this.stateFilter.push({
          text: item.name,
          value: item.id
        })
      })

      this.bugList.forEach(item => {
        if (!this.userFilter.find(user => user.value === item.commitUser)) {
          this.userFilter.push({
            text: this.$utils.userFormatter(item.commitUser, this.userList),
            value: item.commitUser
          })
        }
      })
    },
    getId () {
      return this.getSequence('bug_key')
    },
    onCopy (bug) {
      this.getId().then((id) => {
        this.curData = this.$utils.deepCopy(bug)
        this.curData.id = id
        this.curData.commentId = ''
        this.curData.attachs = []
        this.curData.name = this.curData.name + '[复制]'
        delete this.curData._id
        this.doAdd().then((data) => {
          this.addBug(data)
        })
      })
    },
    hasImage (attachs) {
      return attachs.find(item => item.type.indexOf('image') !== -1)
    },
    hasVedio (attachs) {
      return attachs.find(item => item.type.indexOf('video') !== -1)
    },
    onBugEdit (bug) {
      this.$router.push({ path: 'list/detail', query: { id: bug.id } })
    },
    doAfterDelete (bug) {
      this.deleteBug(bug._id)
      this.doDeleteBugOrbit(bug)
    },
    doDeleteBugOrbit (bug) {
      this.$dataService.delete(this.$apiUrl.BASE_DELETE + 'bugs', { bugId: bug.id })
    },
    levelFormater (row, column, cellValue) {
      return this.$enumService.enumFormat('BUG_LEVEL', cellValue)
    },
    stateFormater (row, column, cellValue) {
      return this.$enumService.enumFormat('BUG_STATE', cellValue)
    },
    filterHandler (value, row, column) {
      const property = column['property']
      return row[property] === value
    },
    ...mapMutations(['deleteBug', 'addBug'])
  }
}
</script>

<style>
  .el-icon-arrow-down:before{
    font-size: 14px;
    margin-left: 5px;
  }
</style>

<style scoped lang="scss">
  .bug-summary{
    cursor: pointer;
  }

  .fa-angle-double-down{
    cursor: pointer;
  }

  .quick-operation{
    cursor: pointer;
    font-size: 16px;

    .fa-trash{
      color: #F56C6C !important;
    }

    .fa{
      margin-right: 5px;
      color: #409EFF;
    }
  }
</style>



